import React, {Component} from 'react'
import {Route} from 'react-router-dom'
import '../styles/Content.css'
import DMChucVu from '../container/DMChucVu'
import Exception from '../components/Exception'
import DMDantoc from '../container/DMDanToc';
import DMNhanVien from '../container/DMNhanVien_Them';
import UploadImg from '../container/UploadImg'
import DMNguoiDung from '../container/DMNguoiDung'

class Content extends Component {
    render() {
        return (
            <div className='content'>
                <Route exact path="/" render={() => <div>Welcome</div>}/>
                <Route path="/dmchucvu" render={() => <DMChucVu {...this.props}/>}/>
                <Route path="/dmdantoc" render={() => <DMDantoc {...this.props}/>}/>
                <Route path="/dmnhanvien_them" render={() => <DMNhanVien {...this.props}/>}/>
                <Route path="/UploadImg" render={() => <UploadImg {...this.props}/>}/>
                <Route path="/dmnguoidung" render={() => <DMNguoiDung {...this.props}/>}/>
                <Route path="/403" render={() => <Exception type='403' {...this.props}/>}/>
                <Route path="/404" render={() => <Exception type='404' {...this.props}/>}/>
                <Route path="/500" render={() => <Exception type='500' {...this.props}/>}/>
            </div>
        )
    }
}

export default Content