import React from 'react'
import '../styles/DMNhanVien_Them.css'
import {
    Card, PageHeader, Typography, Form, Input,  Select,  Button
  } from 'antd';

const { Paragraph } = Typography
const { Option } = Select;
// const AutoCompleteOption = AutoComplete.Option;

class DMNhanVien_Them extends React.Component {
    state = {
      confirmDirty: false,
      autoCompleteResult: [],
    };
    handleSubmit = (e) => {
        e.preventDefault();
        this.props.form.validateFieldsAndScroll((err, values) => {
          if (!err) {
            console.log('Received values of form: ', values);
          }
        });
      }


    render() {
        const { getFieldDecorator } = this.props.form;
        // const { autoCompleteResult } = this.state;

        const formItemLayout = {
          labelCol: {
            xs: { span: 24 },
            sm: { span: 7 },
          },
          wrapperCol: {
            xs: { span: 24 },
            sm: { span: 10 },
          },
        };
        const tailFormItemLayout = {
            wrapperCol: {
              xs: {
                span: 24,
                offset: 0,  
              },
              sm: {
                span: 2,
                offset: 11,
              },
            },
          };
    
        return (
          <div className="layout_themnhanvien">
                <PageHeader title="Cập nhật nhân viên">
                    <Paragraph>
                        Thêm nhân viên
                    </Paragraph>
                </PageHeader>
                <div className="nv-page-content-loading">
                    <div className="page-content">
                    <Card>
                    
                      <Form {...formItemLayout} onSubmit={this.handleSubmit}>
                        <Form.Item label="Mã nhân viên">
                            {getFieldDecorator('manv', {
                                rules: [{
                                    type: '', message: 'The input is not valid Mã nhân viên!',
                                }, {
                                    required: true, message: 'Please input',
                                }],
                            })(
                                <Input />
                            )}
                        </Form.Item>
                        <Form.Item label="Chức vụ">
                            {getFieldDecorator('macv', {
                                rules: [{
                                  required: false, message: '',
                                },],
                            })(
                                <Input/>
                            )}
                        </Form.Item>
                        <Form.Item label="Tên nhân viên">
                            {getFieldDecorator('tennhanvien', {
                                rules: [{
                                  required: false, message: '',
                                },],
                            })(
                                <Input />
                            )}
                        </Form.Item>
                        <Form.Item label="Dân tộc">
                            {getFieldDecorator('madantoc', {
                                rules: [{
                                  required: false, message: '',
                                },],
                            })(
                                <Input />
                            )}
                        </Form.Item>
                        <Form.Item label="Tôn giáo">
                            {getFieldDecorator('matongiao', {
                                rules: [{
                                    required: false, message: '',
                                }],
                            })(
                                <Input />
                            )}
                        </Form.Item>
                        <Form.Item label="Cửa hàng">
                            {getFieldDecorator('macuahang', {
                                rules: [{
                                    required: false, message: '',
                                }],
                            })(
                                <Input />
                            )}
                        </Form.Item>
                        <Form.Item label="Ngày sinh">
                            {getFieldDecorator('ngaysinh', {
                                rules: [{
                                    required: false, message: '',
                                }],
                            })(
                                <Input type="date"/>
                            )}
                        </Form.Item>
                        <Form.Item label="Nơi sinh">
                            {getFieldDecorator('noisinh', {
                                rules: [{ required: true, message: '' }],
                            })(
                                <Input />
                            )}
                        </Form.Item>
                        <Form.Item label="Tạm trú">
                            {getFieldDecorator('tamtru', {
                                rules: [{ required: true, message: '' }],
                            })(
                                <Input />
                            )}
                        </Form.Item>
                        <Form.Item label="Giới tính">
                            {getFieldDecorator('gioitinh', {
                                rules: [{ required: true, message: '' }],
                            })(
                            <Select>
                              <Option value="Nam">Nam</Option>
                              <Option value="Nữ">Nữ</Option>
                            </Select>
                            )}
                        </Form.Item>
                        <Form.Item label="CMND số">
                            {getFieldDecorator('cmnd', {
                                rules: [{ required: true, message: '' }],
                            })(
                                <Input type="number" />
                            )}
                        </Form.Item>
                        <Form.Item label="Ngày nhận việc">
                            {getFieldDecorator('ngaynhanviec', {
                                rules: [{ required: true, message: '' }],
                            })(
                                <Input type="date"/>
                            )}
                        </Form.Item>
                        <Form.Item label="Trình độ văn hóa">
                            {getFieldDecorator('trinhdovh', {
                                rules: [{ required: true, message: '' }],
                            })(
                                <Input />
                            )}
                        </Form.Item>
                        <Form.Item label="Điện thoại">
                            {getFieldDecorator('dienthoai', {
                                rules: [{ required: true, message: '' }],
                            })(
                                <Input type="number"/>
                            )}
                        </Form.Item>
                        <Form.Item label="Email">
                            {getFieldDecorator('email', {
                                rules: [{ required: true, message: '' }],
                            })(
                                <Input />
                            )}
                        </Form.Item>
                        <Form.Item label="Hình">
                            {getFieldDecorator('hinh', {
                                rules: [{ required: true, message: '' }],
                            })(
                                <Input />
                            )}
                        </Form.Item>
                        <Form.Item label="Trạng thái làm việc">
                            {getFieldDecorator('trangthailamviec', {
                                rules: [{ required: true, message: '' }],
                            })(
                              <Select>
                                <Option value="0">Đang làm việc</Option>
                                <Option value="1">Đã nghỉ việc</Option>
                            </Select>
                            )}
                        </Form.Item>
                        <Form.Item label="Lương">
                            {getFieldDecorator('luong', {
                                rules: [{ required: true, message: '' }],
                            })(
                                <Input type="number"/>
                            )}
                        </Form.Item>
                        <Form.Item {...tailFormItemLayout}>
                            <Button type="primary" htmlType="submit">Submit</Button>
                        </Form.Item>
                    </Form>
                    </Card>
                  </div>
                </div>
            </div>
        );
    }
}
const DMNhanVien_Themm = Form.create({ name: 'submit' })(DMNhanVien_Them);
export default DMNhanVien_Themm