import React, {Component} from 'react'
import {PageHeader, Typography, Table, Divider, Button } from 'antd';
import '../styles/DMDanToc.css'

const { Paragraph } = Typography

const columns = [{
    title: 'Name',
    dataIndex: 'name',
    key: 'name',
    // render: text => <a href="#">{text}</a>,
  },
  {
    title: 'Age',
    dataIndex: 'age',
    key: 'age',
  },
  {
    title: 'Address',
    dataIndex: 'address',
    key: 'address',
  }, 
  {
    title: 'Action',
    key: 'action',
    render: (text, record) => (
      <span>
        <Button type="link">Invite {record.name}</Button>
        <Divider type="vertical" />
        <Button type="link">Delete</Button>
      </span>
    ),
  }];
  const data = [{
    key: '1',
    name: 'John Brown',
    age: 32,
    address: 'New York No. 1 Lake Park',
  }, {
    key: '2',
    name: 'Jim Green',
    age: 42,
    address: 'London No. 1 Lake Park111111111',
  }, {
    key: '3',
    name: 'Joe Black',
    age: 32,
    address: 'Sidney No. 1 Lake Park',
  }];

class DMDantoc extends Component {
    render() {
        return (
            <div className="layout">
                <PageHeader title="Danh sách dân tộc">
                    <Paragraph>
                        
                    </Paragraph>
                </PageHeader>
                <div className="page-content-loading">
                    <div className="page-content">
                        <Table columns={columns} dataSource={data} />
                    </div>
                </div>
            </div>
        )
    }
}

export default DMDantoc
