import React, {Component} from 'react'
import '../styles/Header.css'
import {
    Drawer, Menu, Icon, Avatar, Dropdown
} from 'antd'
import {Link} from 'react-router-dom'
import logo from '../assets/login-logo.svg'
import {menuData} from '../data/menu'
import {removeAuthority} from '../utils/authority'

const { SubMenu } = Menu
const rootSubmenuKeys = ['danhmuc', 'exception']

class Header extends Component {
    state = {
        visible: false,
        openKeys: []
    }

    render() {
        const menu = (
            <Menu onClick={this.onMenuUserInfoClick}>
              <Menu.Item key="userCenter">
                <Icon type="user" />
                <span>Account center</span>
              </Menu.Item>
              <Menu.Item key="userinfo">
                <Icon type="setting" />
                <span>Account settings</span>
              </Menu.Item>
              <Menu.Item key="triggerError">
                <Icon type="close-circle" />
                <span>Trigger Error</span>
              </Menu.Item>
              <Menu.Divider />
              <Menu.Item key="logout">
                <Icon type="logout" />
                <span>Logout</span>
              </Menu.Item>
            </Menu>
        )

        return (
            <div className='header'>
                <Drawer
                    visible={this.state.visible}
                    closable={false} placement="left"
                    className='header-drawer'
                    onClose={this.onClose}
                >
                    <div className='header-menu-logo'>
                        <img src={logo} alt="logo" />
                        <h1>QLCHTT</h1>
                    </div>
                    <Menu mode="inline" className='header-menu' theme="dark"
                        onClick={this.onItemClick}
                        openKeys={this.state.openKeys}
                        onOpenChange={this.onOpenChange}
                    >
                        {this.createMenu(menuData)}
                    </Menu>
                </Drawer>
                <span className='icon-collapsed' onClick={this.toggleCollapsed}>
                    <Icon type='menu-unfold'/>
                </span>
                <div className='header-right'>
                    <Dropdown overlay={menu}>
                        <div className='user-info'>
                            <Avatar size='small' className='avatar' icon="user" />
                            <span>User name</span>
                        </div>
                    </Dropdown>
                </div>
            </div>
        )
    }

    createMenu = menuData => {
        return menuData.map(item => this.getSubMenuOrItem(item))
    }

    getSubMenuOrItem = item => {
        if (item.child) {
            return (
                <SubMenu key={item.key} title={
                    <span>
                        <Icon type={item.icon} />
                        <span>{item.title}</span>
                    </span>
                }>
                    {item.child.map(itemChild => this.getSubMenuOrItem(itemChild))}
                </SubMenu>
            )
        }
        return (
            <Menu.Item key={item.key}>
                {item.icon && <Icon type={item.icon} />}
                <span><Link to={item.linkto}>{item.title}</Link></span>
            </Menu.Item>
        )
    }

    onClose = () => {
        this.setState({
            visible: false,
        })
    }

    toggleCollapsed = () => {
        this.setState({
            visible: !this.state.visible,
        })
    }

    onItemClick = () => {
        this.setState({
            visible: !this.state.visible,
        })
    }

    onOpenChange = (openKeys) => {
        const latestOpenKey = openKeys.find(key => this.state.openKeys.indexOf(key) === -1);
        if (rootSubmenuKeys.indexOf(latestOpenKey) === -1) {
            this.setState({ openKeys });
        }
        else {
            this.setState({
                openKeys: latestOpenKey ? [latestOpenKey] : [],
            });
        }
    }

    onMenuUserInfoClick = ({key}) => {
        switch(key) {
            case 'logout':
                removeAuthority()
                this.props.setLoginStatus(false)
                break
            default:
                break
        }
    }
}

export default Header