import React from 'react'
import {PageHeader, Typography, Table, Divider, Button, Modal, Form, Input} from 'antd';
import '../styles/DMNguoiDung.css'

const { Paragraph } = Typography

const columns = [{
    title: 'MaNV',
    dataIndex: 'manv',
    key: 'manv'
    // render: text => <a href="#">{text}</a>,
  },
  {
    title: 'Password',
    dataIndex: 'password',
    key: 'password',
  }, 
  {
    title: 'Action',
    key: 'action',
    render: (text, record) => (
      <span>
        <Button type="link">Invite {record.name}</Button>
        <Divider type="vertical" />
        <Button type="link">Delete</Button>
      </span>
    ),
  }];
  const data = [{
    key: '1',
    manv: '201810999',
    password: 'admin',
  }, {
    key: '2',
    manv: '201977888',
    password: 'admin',
  }, {
    key: '3',
    manv: '201706123',
    password: 'admin',
  }];

class DMNguoiDung extends React.Component {
    state = { visible: false }

    showModal = () => {
        this.setState({
          visible: true,
        });
    }
    handleOk = (e) => {
        console.log(e);
        this.setState({
            visible: false,
        });
    }
    
    handleCancel = (e) => {
        console.log(e);
        this.setState({
            visible: false,
        });
    }

    render() {
        const { getFieldDecorator } = this.props.form;
        
        const formItemLayout = {
            labelCol: {
              xs: { span: 24 },
              sm: { span: 7 },
            },
            wrapperCol: {
              xs: { span: 24 },
              sm: { span: 10 },
            },
          };
        return (
            <div className="layout">
                <PageHeader title="Danh sách Người dùng">
                    <Paragraph>
                        <Button type="primary" onClick={this.showModal}>
                            Thêm Người dùng
                        </Button>
                        <Modal
                            title="Basic Modal"
                            visible={this.state.visible}
                            onOk={this.handleOk}
                            onCancel={this.handleCancel}
                        >
                            <Form {...formItemLayout}> 
                                <Form.Item label="Mã nhân viên">
                                    {getFieldDecorator('manv', {
                                    rules: [{
                                        required: false, message: 'Please input',
                                    }],
                                    })(
                                    <Input />
                                )}
                                </Form.Item>
                                <Form.Item label="Password">
                                    {getFieldDecorator('password', {
                                        rules: [{
                                        required: false, message: '',
                                        },],
                                    })(
                                        <Input/>
                                    )}
                                </Form.Item>
                            </Form>
                        </Modal>
                    </Paragraph>
                </PageHeader>
                <div className="page-content-loading">
                    <div className="page-content">
                        <Table columns={columns} dataSource={data} />
                    </div>
                </div>
            </div>
        )
    }
}

export default DMNguoiDung
