import React from 'react'
import BaseComponent from '../components/BaseComponent'
import '../styles/UploadImg.css'
import {
    Row, Col, Card, PageHeader, Typography, Form, Select, Icon
} from 'antd'
import {imgList7to12,imgList13to18} from '../data/imgListExample'

const { Meta } = Card
const { Paragraph } = Typography
const { Option } = Select

class UploadImg extends BaseComponent {
    state = {
        imgList: []
    }

    render() {
        return (
            <div id='uploadimg'>
                <PageHeader title="Cập nhật hình ảnh">
                    <Paragraph>
                        Cập nhật hình ảnh hằng ngày theo từng mốc thời gian
                    </Paragraph>
                </PageHeader>
                <div className='uploadimg-content'>
                    <Card bordered={false} style={{width:'100%'}}>
                        <Form>
                            <Row>
                                <Col sm={{span:24}} md={{span:8}}>
                                    <Form.Item label="Mốc thời gian">
                                        <Select
                                            placeholder="Lựa chọn mốc thời gian"
                                            onChange={this.handleSelectChange}
                                        >
                                            <Option value="7to12">7h-12h</Option>
                                            <Option value="13to18">13h-18h</Option>
                                            <Option value="18to23">18h-23h</Option>
                                        </Select>
                                    </Form.Item>
                                </Col>
                            </Row>
                        </Form>
                        <Row gutter={24}>
                            {this.createImgList(this.state.imgList)}
                        </Row>
                    </Card>
                </div>
            </div>
        )
    }

    createImgList = imgList => {
        return imgList.map(item => (
            <Col xs={{span:24}} sm={{span:12}} xl={{span:6}} style={{marginBottom:'24px'}}>
                <Card style={{width:'100%'}}
                    cover={<img alt="example" src={item.src}/>}
                    actions={[<Icon type="edit" />]}
                >
                    <Meta title={item.title} description={item.description}/>
                </Card>
            </Col>
        ))
    }

    createEmptyImgList = () => {
        let imgList = []
        let item = (
            <Col xs={{span:24}} sm={{span:12}} xl={{span:6}} style={{marginBottom:'24px'}}>
                <Card style={{width:'100%'}}
                    actions={[<Icon type="upload" />]}
                >
                    <Meta description=''/>
                </Card>
            </Col>
        )
        for(let i = 0; i < 4; i++) {
            imgList.push(item)
        }
        return imgList
    }

    handleSelectChange = (value) => {
        switch(value) {
            case '7to12':
                this.setState({imgList:imgList7to12})
                break
            case '13to18':
                this.setState({imgList:imgList13to18})
                break
            default:
                let imgList = this.createEmptyImgList()
                this.setState({imgList:imgList})
                break
        }
    }
}

export default UploadImg