import React, {Component} from 'react'
import LoginPage from '../container/LoginPage'
import {BrowserRouter} from 'react-router-dom'
import Header from '../components/Header'
import Content from '../components/Content'
import {Spin} from 'antd'
import {USER_MANV} from '../utils/constants'
import {getAuthorityWithKey} from '../utils/authority'

class MainLayout extends Component {
    constructor(props) {
        super(props)
        this.state = {
            loginStatus: false,
            isSpinning: false
        }
        this.event = {
            setLoginStatus: this.setLoginStatus,
            setSpinning: this.setSpinning
        }
    }

    componentWillMount() {
        if(getAuthorityWithKey(USER_MANV)) {
            this.setState({
                loginStatus: true
            })
        }
    }

    render() {
        let ele
        // this.state.loginStatus
        if(true) {
            ele = (
                <Spin spinning={this.state.isSpinning} style={{maxHeight:'100vh',height:'100vh'}}>
                    <BrowserRouter>
                        <div style={{backgroundColor:'rgb(240, 242, 245)',minHeight:'100vh'}} >
                            <Header setLoginStatus={this.setLoginStatus}/>
                            <Content event={this.event}/>
                        </div> 
                    </BrowserRouter>
                </Spin>
            )
        }
        else {
            ele = <LoginPage setLoginStatus={this.setLoginStatus}/>
        }
        return ele
    }

    setLoginStatus = (status) => {
        this.setState({
            loginStatus: status
        })
    }

    setSpinning = (isSpinning) => {
        this.setState({
            isSpinning: isSpinning
        })
    }
}

export default MainLayout